@import "./var";
.TeamSec{
    width: 100%;
    background-color: $mainColor;
    padding: $sectionPading;
}
.slidcountaner{
    margin: 30px;
    padding: 20px;
    border-radius: 10px;
    border: 1px solid $porderColor;
    background: radial-gradient($mainColor,$mainColor2);
    box-shadow: 0 0 5px $shadoColor;
    backdrop-filter: blur(3px);
    color: $fontColor;
}
@media only screen and (max-width: 500px) {
    .TeamSec{
        padding: $sectionPadingMin;
    }
}
